.header {
    display: grid;
    grid-template-columns: 6fr 3fr;
    background-color: rgb(42, 42, 47);
}

div img {
    width: 20%;
}

.offer {
    background: rgb(42, 42, 47);
    height: 50%;
    color: white;
    border: solid 2px white;
    margin-right: 5%;
    margin-left: 5%;
}

.header1 {
    display: flex;
    justify-content: space-around;
    padding: 1%;
}

.header2 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.item {
    color: white;
    text-decoration: none;
    padding: 3%;
    /* padding-top: 3%;
    padding-bottom: 3%; */
}

.active {
    border-bottom: solid 1px white;
    color: white;
    text-decoration: none;
    padding: 3%;
    /* padding-top: 3%;
    padding-bottom: 3%; */
}

NavLink {
    padding-top: 100%;
    border: solid 1px white;
}