.user {
    display: grid;
    grid-template-columns: 1fr 4fr;
    color: white;
}

.pagesBar {
    display: flex;
    justify-content: space-around;
}

.currentPage {
    font-weight: 800;
    border-bottom: solid 1px white;
    color: white;
}

.page {
    color: white;
}

.userPhoto {
    height: 100px;
    width: 100px;
    border-radius: 1000px;
}

h3 {
    grid-column: 2 span;
}

.NavLinkUser {
    text-decoration:  none;
}

.followButton {
    background: greenyellow;
    padding: 2px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 15px;
    padding-right: 15px;
}

.unfollowButton {
    background: rgb(255, 101, 101);
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 15px;
    padding-right: 15px;
}
