.myMessage {
    display: flex;
    justify-content: flex-end;
}

.myMessage p {
    background-color: rgb(24, 126, 243);
    padding: 1%;
    border-radius: 100px 100px 0 100px;
    color: white;
}

.alienMessage {
    display: flex;
    justify-items: flex-start;
}

.alienMessage p {
    background-color: rgb(4, 207, 92);
    padding: 1%;
    border-radius: 100px 100px 100px 0;
    color: white;
}

textarea {
    width: 100%;
}